import { Component, OnInit } from '@angular/core';
import { interval } from 'rxjs';
import { Stream } from '../../app.interface';
import { getStreamObj } from '../../utils';

@Component({
  selector: 'app-interval',
  templateUrl: './interval.component.pug',
})
export class IntervalComponent implements OnInit {
  streams: Stream[];

  ngOnInit() {
    this.createStreams();
  }

  createStreams(): void {
    this.streams = [
      getStreamObj(interval(1000), `interval(1000)`, `   `),
      getStreamObj(interval(2000), `interval(2000)`, `   2 `),
      getStreamObj(interval(3000), `interval(3000)`, `   3 `),
      getStreamObj(interval(4000), `interval(4000)`, `   4 `),
      getStreamObj(interval(5000), `interval(5000)`, `   5 `),
    ];
  }
}
